
/* style for the reusable card components */
/* primary use is the work page */

.cardBody img{
  position: relative;
  left:5px;
}

.cardfooter{
  padding:3.5% 4.5% 5.5%;
  min-height:165px;
}

.cardfooter h4{
  margin:15px 0 20px;
}

.cardfooter p{
  font-weight: normal;
  font-size:.9em;
  display:block;
  padding:5px 0 10px;
}

a .cardfooter {
  font-weight: bold;
  color:#333;
}

a.cardwrapper{
  width:32%;
  background-color: #e2e2e2;
  margin:1% 1% 1% 0;
  text-decoration: none;
  overflow: hidden;
  height:625px;
  cursor: pointer;
}

a.cardwrapper:hover{
  transform: scale(1.01);
}

a.cardwrapper:hover img{
  filter: none;
}

.miniCardFooter{

  overflow: auto;
  white-space: nowrap;
  overflow-x: scroll;
  padding:40px 8.333%;
  margin-top:50px;
}

.miniCardFooter img{
height:200px;
}

.miniCardFooter .cardwrapper{
  width:150px;
  display: inline-block;
  height: 190px;
}

.miniCardFooter h4{
  font-size: 1em;
  white-space: break-spaces;
}

.miniCardFooter .cardfooter{
  min-height:0;
}

.miniCardFooter .deliniator, .miniCardFooter p{
  display: none;
}

.miniCardFooter .cardwrapper h6{
  font-size:.8em;
}


@media only screen and (max-width: 767px) {
  a.cardwrapper {
    width: 48%;
  }
  
}

@media only screen and (max-width : 480px) {
  a.cardwrapper {
    width: 48%;
    margin:2% 2% 3% 0;
   }
  }
