
.roleList{
    list-style: none;
    padding:0;
    margin:0;
}

.roleList li{
    color:#333;
    font-size: .95em;;
}

.caseLead{
    margin:0 auto 40px; 
    padding:0 8.333%;
  }

  .enterprise .caseLead .g7 p{display:none;}

  .caseStudyGrid{display: flex;
    flex-wrap: wrap;
    flex-basis: auto;
    flex-direction: row;
    list-style: none;
    padding:0; }

.workCaseStudyGrid{
    display: flex;
    flex-wrap: wrap;
    flex-basis: auto;
    flex-direction: row;
    list-style: none;
    padding:0; 
    margin:3em 0 5em;
}

.caseStudyGrid-12{
    display: flex;
    flex-wrap: wrap;
    flex-basis: auto;
    flex-direction: row;
    list-style: none;
    padding:0; 
}

.portfolioRow{
    margin-top:2%;
}


.addgridpad{
    padding:.4%;

}


.numberOver{
    position: absolute; 
    right:16px; 
    bottom:16px; 
    z-index: 1;
    background-color:#333;
    color:#fff;
    font-weight: bold;
    font-size: 1em;
    border-radius: 20px;
    display:block;
    width:28px;
    height:28px;
    text-align: center;
    line-height: 1.8em;
}

.gridKey li{
    float:left;
    color:rgb(129, 129, 129); 
    font-size: .9em;
    margin-right:36px;
}



.card img{
    height:850px;
    filter: grayscale(50%);
}

.contentBody.homehero img{width:100%; height:auto;}

.contentBodyCallout{
    background-color: #fff;
    color:#000;
    padding:3em 5%;
}

.caseStudyThreeCol{
    display: flex;
    flex-wrap: wrap;
    flex-basis: auto;
    flex-direction: row;
    justify-content: center;
    
}

.caseStudyCallOut{
    background-color: #fff;
    padding:24px 8.333%;
    margin-top:56px;
    
}

.caseStudyCallOut ul{
    padding:18px;
    color:#444;
    font-size:1em;
    line-height: 150%;
}

.caseStudyCallOut p{
    color:#444;
    font-size:1em;
    line-height: 150%;
}

.caseStudyThreeCol h4{
    font-family: 'Playfair Display', serif;
}

.caseStudyThreeCol div{
    width:30%;
    padding-right:3%;
}

.blockQuoteCallout{
    font-size:2em;
    font-family: 'Playfair Display', serif;
    font-style: italic;
    color:#E63946;
    margin-top:40px;

}

/* gridimage is for hero grid on individual case study pages */

.gridimage{
    position: relative;
}
.gridimage img{
    width:100%; 
} 

@media only screen and (max-width: 767px) {

   
    
    .numberOver{
        position: absolute; 
        right:6px; 
        bottom:6px; 
        z-index: 1;
        background-color:#333;
        color:#fff;
        font-weight: bold;
        font-size: .6em;
        border-radius: 20px;
        display:block;
        width:18px;
        height:18px;
        text-align: center;
        line-height: 1.8em;
    }


    .caseStudyThreeCol div{
        width:90%;
        margin:auto;
    }
    .gridKey li{
        float: none;
    }

    .caseStudyCallOut{
        margin-top:24px;
        clear:left;
    }

    .caseStudyGrid-12 .gridimage {
        width:25%; 
    }
 }

 @media only screen and (max-width : 480px) {

    .caseStudyGrid-12 .gridimage {
        width:33.333%; 
    }
    }