/* gridimage is for hero grid on individual case study pages */

.gridimage{
    position: relative;
}
.gridimage img{
    width:100%; 
} 

@media only screen and (max-width: 767px) {

   
    
    .numberOver{
        position: absolute; 
        right:6px; 
        bottom:6px; 
        z-index: 1;
        background-color:#333;
        color:#fff;
        font-weight: bold;
        font-size: .6em;
        border-radius: 20px;
        display:block;
        width:18px;
        height:18px;
        text-align: center;
        line-height: 1.8em;
    }


    .caseStudyThreeCol div{
        width:90%;
        margin:auto;
    }
    .gridKey li{
        float: none;
    }

    .caseStudyCallOut{
        margin-top:24px;
        clear:left;
    }

    .caseStudyGrid-12 .gridimage {
        width:25%; 
    }
 }

 @media only screen and (max-width : 480px) {

    .caseStudyGrid-12 .gridimage {
        width:33.333%; 
    }
    }

/* style for the reusable card components */
/* primary use is the work page */

.cardBody img{
  position: relative;
  left:5px;
}

.cardfooter{
  padding:3.5% 4.5% 5.5%;
  min-height:165px;
}

.cardfooter h4{
  margin:15px 0 20px;
}

.cardfooter p{
  font-weight: normal;
  font-size:.9em;
  display:block;
  padding:5px 0 10px;
}

a .cardfooter {
  font-weight: bold;
  color:#333;
}

a.cardwrapper{
  width:32%;
  background-color: #e2e2e2;
  margin:1% 1% 1% 0;
  text-decoration: none;
  overflow: hidden;
  height:625px;
  cursor: pointer;
}

a.cardwrapper:hover{
  transform: scale(1.01);
}

a.cardwrapper:hover img{
  -webkit-filter: none;
          filter: none;
}

.miniCardFooter{

  overflow: auto;
  white-space: nowrap;
  overflow-x: scroll;
  padding:40px 8.333%;
  margin-top:50px;
}

.miniCardFooter img{
height:200px;
}

.miniCardFooter .cardwrapper{
  width:150px;
  display: inline-block;
  height: 190px;
}

.miniCardFooter h4{
  font-size: 1em;
  white-space: break-spaces;
}

.miniCardFooter .cardfooter{
  min-height:0;
}

.miniCardFooter .deliniator, .miniCardFooter p{
  display: none;
}

.miniCardFooter .cardwrapper h6{
  font-size:.8em;
}


@media only screen and (max-width: 767px) {
  a.cardwrapper {
    width: 48%;
  }
  
}

@media only screen and (max-width : 480px) {
  a.cardwrapper {
    width: 48%;
    margin:2% 2% 3% 0;
   }
  }



